import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  SIGN_IN_REQUEST,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAILURE,
} from "../signin/signinConstants";

export function postSignupAction(signupObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/register`,
      method: "POST",
      body: JSON.stringify(signupObject),
      types: [
        { type: SIGN_IN_REQUEST, meta: { showLoader: true } },
        { type: SIGN_IN_SUCCESS, meta: { showLoader: false } },
        { type: SIGN_IN_FAILURE, meta: { showLoader: false } },
      ],
    },
  };
}
