import {
  SIGN_IN_SUCCESS,
  LOGOUT_SUCCESS,
  SET_USER_DATA,
} from "./signinConstants";

const initialState = {
  userDetails: null,
  userToken: null,
  is_registeration_complete: 0,
  isUserSignedIn: false,
};

export default function signInReducer(state = initialState, { type, payload }) {
  switch (type) {
    case SIGN_IN_SUCCESS:
      if (payload.code === 200 || payload.code === 201) {
        return {
          ...state,
          userDetails: payload.data.user_details,
          is_registeration_complete: payload.data.is_registeration_complete,
          userToken: payload.data.user_token,
          isUserSignedIn: true,
        };
      }

      return { ...state };

    case SET_USER_DATA:
      return {
        ...state,
        userDetails: payload.user_details,
        is_registeration_complete:
          payload.user_details.is_registeration_complete,
        userToken: payload.user_token,
        isUserSignedIn: true,
      };

    case LOGOUT_SUCCESS:
      if (payload.code === 200 || payload.code === 201) {
        return {
          ...state,
          userDetails: null,
          userToken: null,
          is_registeration_complete: 0,
          isUserSignedIn: false,
        };
      }

      return { ...state };

    default:
      return state;
  }
}
