import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_PROFILE_DETAILS_REQUEST,
  GET_PROFILE_DETAILS_SUCCESS,
  GET_PROFILE_DETAILS_FAILURE,
  EDIT_PROFILE_DETAILS_REQUEST,
  EDIT_PROFILE_DETAILS_SUCCESS,
  EDIT_PROFILE_DETAILS_FAILURE,
  EDIT_PROFILE_REQUEST,
  EDIT_PROFILE_SUCCESS,
  EDIT_PROFILE_FAILURE,
} from "./constants";

export function getProfileDetailsAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/show`,
      method: "GET",
      body: undefined,
      types: [
        GET_PROFILE_DETAILS_REQUEST,
        GET_PROFILE_DETAILS_SUCCESS,
        GET_PROFILE_DETAILS_FAILURE,
      ],
    },
  };
}

export function editProfileDetailsAction(updatedProfileObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/update`,
      method: "POST",
      body: JSON.stringify(updatedProfileObject),
      types: [
        EDIT_PROFILE_DETAILS_REQUEST,
        EDIT_PROFILE_DETAILS_SUCCESS,
        EDIT_PROFILE_DETAILS_FAILURE,
      ],
    },
  };
}

export function editProfileAction(updatedProfileObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/update-profile`,
      method: "POST",
      body: JSON.stringify(updatedProfileObject),
      types: [EDIT_PROFILE_REQUEST, EDIT_PROFILE_SUCCESS, EDIT_PROFILE_FAILURE],
    },
  };
}
